body {
    margin: 0;
    padding: 0;
    font-family: $secondary-font;
    font-size: 14px;
}

img {
    height: auto;
    max-width: 100%;
}

* {
    box-sizing: border-box;
}

.title-main {
    font-size: 24px;
    letter-spacing: 1px;
    color: $black;
    font-weight: 700;
    text-align: left;
    margin-bottom: 30px;
}

.title {
    font-size: 14px;
    letter-spacing: 1px;
    line-height: 24px;
    color: $black-light;
    font-family: $secondary-font;
    font-weight: 700;
    font-family: "Lato";
}

.slick-dots {
    padding-left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
    li {
        list-style-type: none;
        margin-right: 10px;
        button {
            font-size: 0;
            height: 10px;
            width: 20px;
            background-color: $black-light;
            border: none;
            border-radius: 5px;
            transition: all .5s ease 0s;
        }
        &.slick-active {
            button {
                width: 35px;
                transition: all .5s ease 0s;
            }
        }
    }
}