header {
    margin-bottom: 50px;
    .tabs {
        display: flex;
        justify-content: space-between;
        @media (max-width: 991.5px) {
            flex-wrap: wrap;
        }
        a {
            padding: 20px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-radius: 10px;
            border: 1px solid $black;
            margin-right: 15px;
            text-decoration: none;
            transition: all .5s ease 0s;
            @media (max-width: 991.5px) {
                width: calc(50% - 15px / 2);
                margin-bottom: 15px;
                &:nth-child(2n) {
                    margin-right: 0;
                }
            }
            @media (max-width: 767px) {
                width: 100%;
                margin: 0;
                margin-bottom: 15px;
                padding-top: 12px;
            }
            span,
            i {
                color: $black-light;
                transition: all .5s ease 0s;
            }
            i {
                font-size: 25px;
            }
            &:last-child {
                margin-right: 0;
            }
            &:hover {
                background-color: $black-light;
                transition: all .5s ease 0s;
                span,
                i {
                    color: $white;
                    transition: all .5s ease 0s;
                }
            }
        }
    }
    .header-in {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 30px;
        margin-top: 30px;
        @media (max-width: 991.5px) {
            flex-direction: column;
        }
        .left {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            @media (max-width: 991.5px) {
                flex-direction: column;
            }
            width: 70%;
            @media (max-width: 991.5px) {
                width: 100%;
            }
            .logo {
                margin-right: 20px;
                @media (max-width: 991.5px) {
                    margin-right: 0;
                    margin-bottom: 15px;
                }
            }
            .location {
                margin-right: 20px;
                @media (max-width: 991.5px) {
                    margin-right: 0;
                    margin-bottom: 15px;
                }
                button {
                    font-size: 14px;
                    min-height: 50px;
                    border-radius: 30px;
                    min-width: 170px;
                    padding-right: 50px;
                    padding-left: 0;
                    background-color: transparent;
                    color: $black-light;
                    border-radius: 22px;
                    filter: drop-shadow(0 0 6.5px rgba(0, 0, 0, 0.19));
                    background-color: $white;
                    border: 1px solid $white;
                    &:focus {
                        outline: none;
                    }
                    &::after {
                        right: 0;
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        right: 15px;
                    }
                }
            }
            .search {
                width: 50%;
                @media (max-width: 991.5px) {
                    width: 100%;
                    margin-bottom: 15px;
                }
                .input-group {
                    position: relative;
                    input {
                        border-radius: 30px;
                        font-size: 14px;
                        min-height: 50px;
                        padding-right: 50px;
                        padding-left: 20px;
                        filter: drop-shadow(0 0 6.5px rgba(0, 0, 0, 0.19));
                        background-color: $white;
                    }
                }
                button {
                    min-width: auto;
                    height: auto;
                    border-radius: none;
                    filter: unset;
                    background-color: transparent;
                    border: none;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: $black-light;
                    position: absolute;
                    right: 10px;
                    top: 11px;
                }
            }
        }
        .right {
            display: flex;
            align-items: center;
            justify-content: center;
            @media (max-width: 991.5px) {
                flex-wrap: wrap;
            }
            a {
                margin-left: 20px;
                color: $black-light;
                text-decoration: none;
                &:nth-child(1) {
                    margin-left: 0;
                }
                &.btn {
                    color: $white;
                }
            }
        }
    }
}