.article {
    .item {
        margin-bottom: 30px;
        position: relative;
        overflow: hidden;
        transition: all .5s ease 0s;
        .img-wrap {
            margin-bottom: 15px;
        }
        .profile {
            img {
                max-width: 40px;
                margin-right: 10px;
                transition: all .5s ease 0s;
            }
        }
        .featured-icon {
            position: absolute;
            top: -45px;
            right: -76px;
            transform: rotate( 45deg);
            background: $light-black;
            height: 112px;
            width: 171px;
            align-items: flex-end;
            display: flex;
            justify-content: center;
            padding-bottom: 13px;
            color: $white;
        }
        &:hover {
            .img-wrap {
                img {
                    -webkit-box-shadow: -1px 3px 9px 7px rgba(187, 187, 187, 0.67);
                    box-shadow: -1px 3px 9px 7px rgba(187, 187, 187, 0.67);
                    transition: all .5s ease 0s;
                }
            }
        }
        &.ovarlay {
            .img-wrap {
                margin-bottom: 0;
            }
            .text-wrap {
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                .profile {
                    width: 100%;
                    padding: 20px;
                    background-color: rgba(0, 0, 0, .5);
                    span {
                        color: $white;
                    }
                }
            }
        }
    }
}

.trips {
    .item {
        position: relative;
        display: inline-block;
        width: 100%;
        margin-bottom: 30px;
        transition: all .5s ease 0s;
        &:hover {
            -webkit-box-shadow: -1px 3px 9px 7px rgba(187, 187, 187, 0.67);
            box-shadow: -1px 3px 9px 7px rgba(187, 187, 187, 0.67);
            transition: all .5s ease 0s;
        }
        .text-wrap {
            position: absolute;
            bottom: 0;
            padding: 15px;
            background-color: rgba(0, 0, 0, .5);
            color: $white;
            width: 100%;
            .title {
                color: $white;
                text-transform: uppercase;
                margin-bottom: 0;
            }
        }
        .days {
            position: absolute;
            right: 15px;
            top: 15px;
            height: 30px;
            width: 90px;
            border-radius: 10px;
            background: $white;
            text-align: center;
            align-items: center;
            display: flex;
            justify-content: center;
            font-weight: bold;
        }
    }
}

.top-hotels,
.slider-hotels {
    margin-bottom: 50px;
    .items {
        overflow: hidden;
        .item {
            display: inline-block;
            width: 100%;
            .title {
                margin-bottom: 8px;
            }
            .img-wrap {
                margin-bottom: 20px;
            }
            .location {
                font-size: 14px;
                display: flex;
                align-items: center;
                letter-spacing: 1px;
                font-weight: 400;
                i {
                    font-size: 20px;
                    margin-right: 10px;
                }
            }
        }
    }
}

.banner {
    position: relative;
    min-height: 600px;
    display: inline-block;
    overflow: hidden;
    width: 100%;
    margin-bottom: 70px;
    &::before {
        content: "";
        background-image: url(../../img/banner.png);
        height: 100%;
        width: 100%;
        display: inline-block;
        background-repeat: no-repeat;
        position: absolute;
        background-size: cover;
    }
    .text-content {
        z-index: 2;
        position: absolute;
        text-align: left;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        max-width: 600px;
        @media (max-width: 991.5px) {
            top: 50%;
            left: 50%;
            right: auto;
            transform: translate(-50%, -50%);
            text-align: center;
        }
        .button-wrap {
            text-align: left;
        }
        .title-bg,
        p {
            font-family: $secondary-font;
            color: $white;
        }
        .title-bg {
            font-size: 90px;
            color: $white;
            filter: drop-shadow(1.362px 2.673px 3.5px rgba(0, 0, 0, 0.35));
            @media (max-width: 991.5px) {
                font-size: 60px;
            }
        }
        p {
            font-size: 30px;
            span {
                font-size: 50px;
            }
        }
    }
}

.destination {
    margin-bottom: 50px;
    .items {
        overflow: hidden;
        .item {
            display: inline-block;
            width: 100%;
            position: relative;
            &::before {
                content: '';
                background: linear-gradient(0deg, rgba(50, 50, 50, 0.6), rgba(50, 50, 50, 0.0) 60%) no-repeat;
                height: 100%;
                width: 100%;
                position: absolute;
                bottom: 0;
                left: 0;
                top: 0;
                right: 0;
            }
            img {
                width: 100%;
                max-height: 500px;
                height: 100%;
                background-size: cover;
                object-fit: cover;
                object-position: top;
            }
            .text-wrap {
                position: absolute;
                bottom: 0;
                padding: 20px;
                .title {
                    font-family: $secondary-font;
                    font-size: 23px;
                    color: $white;
                }
                .description {
                    font-size: 13px;
                    letter-spacing: 1px;
                    line-height: 23px;
                    color: $white;
                    font-weight: 400;
                    font-family: $secondary-font;
                }
            }
        }
    }
}

.slider-testimonial {
    min-height: 277px;
    border-radius: 20px;
    position: relative;
    overflow: hidden;
    padding-top: 20px;
    margin-bottom: 70px;
    @media (max-width: 991.5px) {
        background-color: $white;
        border: 1px solid $gray-light;
    }
    &::before {
        content: "";
        height: 100%;
        background-color: $light-white;
        border: 1px solid $white;
        width: calc(100% - 40px);
        height: 270px;
        display: inline-block;
        position: absolute;
        left: 0;
        right: 0;
        border-radius: 40px;
        margin-left: auto;
        @media (max-width: 991.5px) {
            display: none;
        }
    }
    .item-wrap {
        display: inline-block;
        width: 100%;
        position: relative;
        .item {
            position: relative;
            min-height: 250px;
            background-color: $white;
            border: 1px solid $gray-light;
            margin-left: 0;
            border-radius: 20px;
            display: flex;
            margin-top: -20px;
            max-width: 65%;
            padding: 80px 120px 80px 104px;
            @media (max-width: 991.5px) {
                width: 100%;
                max-width: 100%;
                background-color: transparent;
                border: none;
                margin-top: 0;
                padding: 80px 15px 80px 15px;
                flex-direction: column;
            }
            &::before {
                content: "";
                height: 50px;
                width: 50px;
                background-image: url(../../img/top.png);
                background-size: contain;
                display: inline-block;
                position: absolute;
                left: 33px;
                background-repeat: no-repeat;
                top: 20px;
                @media (max-width: 991.5px) {
                    left: 15px;
                    top: 15px;
                }
            }
            &::after {
                content: "";
                height: 50px;
                width: 50px;
                background-image: url(../../img/bottom.png);
                background-size: contain;
                display: inline-block;
                position: absolute;
                right: 120px;
                background-repeat: no-repeat;
                bottom: 20px;
                @media (max-width: 991.5px) {
                    right: 15px;
                    bottom: 0;
                }
            }
            .img-wrap {
                position: absolute;
                right: -80px;
                top: 50%;
                transform: translateY(-50%);
                @media (max-width: 991.5px) {
                    position: static;
                    transform: unset;
                    right: auto;
                    top: auto;
                    text-align: center;
                    margin-top: 30px;
                    img {
                        max-width: 100px;
                    }
                }
            }
        }
        .detail {
            position: absolute;
            right: 10%;
            top: 36%;
            @media (max-width: 991.5px) {
                width: 100%;
                max-width: 100%;
                position: static;
                text-align: right;
                padding: 20px;
            }
            .title {
                font-size: 24px;
                line-height: 30px;
            }
        }
    }
    .slick-dots {
        position: absolute;
        bottom: 50px;
        right: 7%;
        @media (max-width: 991.5px) {
            display: none !important;
        }
    }
}

.footer {
    .footer-links {
        ul {
            padding-left: 0;
            li {
                list-style-type: none;
                margin-bottom: 10px;
                .fa {
                    margin-right: 10px;
                }
                a {
                    color: $black-light;
                    text-decoration: none;
                }
            }
        }
        .activites {
            ul {
                li {
                    &::before {
                        content: "\f0da";
                        font: normal normal normal 14px/1 FontAwesome;
                        margin-right: 10px;
                    }
                }
            }
        }
    }
    .logo {
        margin-bottom: 20px;
    }
    .bottom-footer {
        .description {
            padding-top: 50px;
            margin-top: 50px;
            border-top: 1px solid $light-white;
            text-align: center;
            line-height: 25px;
            @media (max-width: 991.5px) {
                padding-top: 20px;
                margin-top: 20px;
            }
        }
        .logo {
            text-align: center;
            margin-top: 30px;
        }
    }
    .social {
        ul {
            display: flex;
            align-items: center;
            li {
                margin-right: 15px;
                a {
                    height: 30px;
                    width: 30px;
                    background-color: $light-black;
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all .5s ease 0s;
                    i {
                        color: $white;
                        margin-right: 0 !important;
                    }
                    &:hover {
                        background-color: $black;
                        transition: all .5s ease 0s;
                    }
                }
            }
        }
    }
}

.img-wrap {
    img {
        width: 100%;
    }
}