.btn {
    min-width: 166px;
    height: 50px;
    border-radius: 25px;
    filter: drop-shadow(2.27px 4.455px 6.5px rgba(0, 0, 0, 0.19));
    background-color: $light-black;
    border: 1px solid $white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.button-wrap {
    justify-content: center;
    display: flex;
    text-align: center;
    margin-bottom: 40px;
}